@import url(https://fonts.googleapis.com/css?family=Saira+Condensed&display=swap);
html {
    box-sizing: border-box;
}

*,
*:before,
*:after {
    box-sizing: inherit;
}

body {
    background-color: #f3f2ed;
    height: 100%;
    margin: 0;
    padding: 0;
    padding: 80px 0;
    text-align: center;
    font-family: 'Saira Condensed', sans-serif;
    color: white;
}
@-webkit-keyframes gradientBG {
    0% {
        background-position: 0% 50%;
    }
    50% {
        background-position: 100% 50%;
    }
    100% {
        background-position: 0% 50%;
    }
}

@keyframes gradientBG {
    0% {
        background-position: 0% 50%;
    }
    50% {
        background-position: 100% 50%;
    }
    100% {
        background-position: 0% 50%;
    }
}

.card {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    background: linear-gradient(160deg, rgba(136, 209, 252, 1) 0%, rgba(84, 69, 253, 1) 100%);
    border-radius: 8px;
    height: 100%;
    padding: 15px 25px;
    -webkit-animation: gradientBG 15s ease infinite;
            animation: gradientBG 15s ease infinite;
}

.card.is-clear-day {
    background: linear-gradient(160deg, rgba(247, 183, 51, 1) 0%, rgba(252, 74, 26, 1) 100%);
}

.card.is-clear-night {
    background: linear-gradient(18deg, rgba(182, 136, 252, 1) 0%, rgba(73, 43, 176, 1) 100%);
}

.card.is-rain {
    background: linear-gradient(18deg, rgba(0, 110, 213, 1) 0%, rgba(162, 171, 177, 1) 74%);
}

.card.is-sleet {
    background: linear-gradient(18deg, rgba(0, 110, 213, 1) 0%, rgba(162, 171, 177, 1) 74%);
}

.card.is-snow {
    background: linear-gradient(18deg, rgba(0, 175, 245, 1) 0%, rgba(0, 223, 247, 1) 74%);
}

.card.is-fog {
    background: linear-gradient(18deg, rgba(148, 190, 195, 1) 0%, rgba(135, 192, 228, 1) 74%);
}

.card.is-cloudy {
    background: linear-gradient(18deg, rgba(63, 174, 187, 1) 0%, rgba(0, 157, 255, 1) 100%);
}

.card.is-partly-cloudy-day {
    background: linear-gradient(18deg, rgba(0, 190, 254, 1) 17%, rgba(255, 190, 0, 1) 100%);
}

.card.is-partly-cloudy-night {
    background: linear-gradient(18deg, rgba(121, 0, 254, 1) 17%, rgba(136, 203, 251, 1) 100%);
}

.card.is-default {
    background: -webkit-gradient(linear, left bottom, left top, from(rgba(34, 193, 195, 1)), to(rgba(253, 187, 45, 1)));
    background: linear-gradient(0deg, rgba(34, 193, 195, 1) 0%, rgba(253, 187, 45, 1) 100%);
}

.card-day {
    font-size: 32px;
    margin-bottom: 40px;
    text-align: left;
}

.card-line {
    border-bottom: 3.5px solid #fff;
    width: 100%;
}

.card-temperature {
    font-size: 30px;
    margin-top: 25px;
    text-align: left;
}

.card-row {
    align-items: baseline;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 100%;
}

.card-temperature .small {
    font-size: 20px;
}

.card-city {
    font-size: 36px;
    margin-top: 8px;
    text-align: left;
}

.card-city.dark {
    color: black;
}

.card-options {
    background: none;
    border: none;
    outline: none;
    cursor: pointer;
}

.card-options img {
    -webkit-transition: all .3s ease;
    transition: all .3s ease;
}

.card-options:hover img {
    -webkit-transform: rotateZ(180deg);
            transform: rotateZ(180deg);
}

.card-weather {
    font-size: 30px;
}

.card-back {
    background: linear-gradient(18deg, rgba(0, 110, 213, 1) 0%, rgba(162, 171, 177, 1) 74%);
    border-radius: 8px;
    display: flex;
    flex-direction: column;
    height: 100%;
    justify-content: space-between;
    padding: 20px;
    width: 100%;
}

.credits {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    font-size: 14px;
    color: lightgray;
}

.credits a {
    color: whitesmoke;
    text-decoration: none;
}
.seachBar-wrapper {
    position: fixed;
    top: 0;
    left: 20px;
    right: 20px;
}

.searchBar {
    position: relative;
    background-color: transparent;
    z-index: 999;
    width: 100%;
}

.list-wrapper {
    overflow-y: scroll;
    scrollbar-width: none; /* Firefox */
    -ms-overflow-style: none;  /* IE 10+ */
    margin: 0 0 10px 0;
    min-height: 322px;
    position: relative;
    margin-top: 60px;
}

.list {
    color: #fff;
    text-align: left;
    list-style-type: none;
    font-size: 24px;
    padding: 0 10px;
    flex: 1 1;
    margin-top: 0;
}
.list-wrapper::-webkit-scrollbar { /* WebKit */
    width: 0;
    height: 0;
}
.list-item {
    padding-left: 20px;
    cursor: pointer;
    position: relative;
    -webkit-transition: all .3s ease;
    transition: all .3s ease;
}

.list-item:hover{
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
}

.list-item.is-selected:after {
    background: url(/darksky-weatherapp/static/media/checkmark.501201c3.svg);
    background-size: 100%;
    content: ' ';
    display: block;
    height: 15px;
    left: -4px;
    position: absolute;
    top: 12px;
    width: 15px;
}

.list-item.is-selected {
    color: black;
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
}




/*
A material design form field
Expects this html (note the required attribute on the input):
<div class="material-form-field">
<input type="text" required/>
<label class="material-form-field-label">Form field title</label>
</div>
*/
.material-form-field {
  font-family: 'Roboto', sans-serif;
  font-size: 14px;
  position: relative;
  display: block;
  color: white;
  padding: 25px 0 10px 0;
}
.material-form-field::after {
  content: "\00A0";
  display: block;
  color: #d50000;
  font-size: 12px;
  padding-top: 5px;
}
.material-form-field .material-form-field-label {
  position: absolute;
  display: block;
  top: 35px;
  left: 0px;
  transition: 0.2s ease all;
  -moz-transition: 0.2s ease all;
  -webkit-transition: 0.2s ease all;
  pointer-events: none;
  cursor: text;
}
.material-form-field input {
  padding: 10px 0 10px 0;
  display: block;
  width: 100%;
  border: none;
  font-size: 14px;
  color: white;
  border-bottom: 1px solid white;
  outline: none;
}
.material-form-field input:invalid {
  outline: none;
  box-shadow: none;
}
.material-form-field input:valid ~ .material-form-field-label, .material-form-field input:focus ~ .material-form-field-label {
  top: 10px;
  font-size: 12px;
}
.material-form-field input:focus {
  outline: none;
  color: white;
  padding-bottom: 8px;
  border-bottom-width: 3px;
  border-bottom-color: white;
}
.material-form-field input:focus ~ .material-form-field-label {
  color: white;
}
.material-form-field input:focus ~ .material-dropdown {
  /*
  If you want to animate this you will need to use some js to calculate it
  Optionally, you can simply display:block/none 
  */
  height: 228px;
  box-shadow: 0px 0px 2px 2px rgba(0, 0, 0, 0.2);
  transition: 0.2s ease-out height, step-start box-shadow 0.2s;
  -moz-transition: 0.2s ease-out height, step-start box-shadow 0.2s;
  -webkit-transition: 0.2s ease-out height, step-start box-shadow 0.2s;
}
.material-form-field.material-form-field-invalid {
  color: #d50000;
}
.material-form-field.material-form-field-invalid::after {
  content: attr(data-validationError);
}
.material-form-field.material-form-field-invalid input:focus ~ .material-form-field-label {
  color: #d50000;
}
.button {
    padding: 3px;
    width: 100%;
    border-radius: 8px;
    font-size: 24px;
    border: none;
    cursor: pointer;
    color: black;
    font-family: "Saira Condensed";
    background-color: white;
    -webkit-appearance: none;
    -moz-appearance: none;
}
.panel {
  border-radius: 8px;
  margin: 0 auto;
  width: 320px;
  height: 480px;
  -webkit-perspective: 600px;
          perspective: 600px;
  position: relative;
 }

.panel .panel-front {
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  border-radius: 8px;
  box-shadow: 0 1px 8px rgba(0,0,0,0.4);
  height: 100%;
  left: 0;
  top: 0;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
  -webkit-transform: rotateX(0) rotateY(0);
          transform: rotateX(0) rotateY(0);
  -webkit-transition: all .4s ease-in-out;
  transition: all .4s ease-in-out;
  width: 100%;
}

.panel.flip .panel-front {
  -webkit-transform: rotateY(179deg);
          transform: rotateY(179deg);
}

.panel .panel-back {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  -webkit-transform: rotateY(-179deg);
          transform: rotateY(-179deg);
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  -webkit-transition: all .4s ease-in-out;
  transition: all .4s ease-in-out;
}
.panel.flip .panel-back {
  -webkit-transform: rotateX(0) rotateY(0);
          transform: rotateX(0) rotateY(0);
  box-shadow: 0 15px 50px rgba(0,0,0,0.2);
}

